const indent = (value) => {
    if(value) {
        let arry = value.split('\n');
        arry.forEach((el,index) => {
            if(el && !el.startsWith(' ')) {
                arry[index] = `      ${ el }`
            }
        });
        return arry.join('\n')
    }
    return ''
}
export {
    indent
}